/**
 * useShellBanners Hook Tests
 * File: useShellBanners.test.js
 */
import React from 'react';
import { render, waitFor } from '@testing-library/react-native';
import useShellBanners from '@hooks/useShellBanners';
import useNetworkBanner from '@hooks/useNetworkBanner';

jest.mock('@hooks/useI18n', () => ({
  __esModule: true,
  default: () => ({
    t: (key) => key,
    locale: 'en',
  }),
}));

jest.mock('@hooks/useNetworkBanner', () => jest.fn(() => ({
  isOffline: false,
  showOnlineBanner: false,
})));

const TestComponent = ({ onResult, overrides }) => {
  const result = useShellBanners(overrides);

  React.useEffect(() => {
    onResult(result);
  });

  return null;
};

describe('useShellBanners', () => {
  beforeEach(() => {
    jest.clearAllMocks();
  });

  it('returns empty banners when no conditions are active', async () => {
    let result;
    render(<TestComponent onResult={(value) => (result = value)} />);

    await waitFor(() => {
      expect(result).toEqual([]);
    });
  });

  it('returns maintenance, offline, and online banners when active', async () => {
    useNetworkBanner.mockImplementationOnce(() => ({
      isOffline: true,
      showOnlineBanner: true,
    }));

    let result;
    render(
      <TestComponent
        overrides={{ maintenanceMode: true, maintenanceMessage: 'Maint window' }}
        onResult={(value) => (result = value)}
      />
    );

    await waitFor(() => {
      expect(result).toHaveLength(3);
      expect(result[0].id).toBe('maintenance');
      expect(result[1].id).toBe('offline');
      expect(result[2].id).toBe('online');
    });
  });
});
